@import "../../assets/styles/variables.scss";
.header-menu__button {
	width: 60px;
	height: 60px;
	color: $mainDarkColor;
	border-radius: 50%;
	transition: background-color 0.3s ease-in-out;
	@media (any-hover: hover) {
		&:hover {
			background-color: $hoverBackgroundColor;
		}
	}
	&.active {
		.top {
			stroke-dashoffset: -132px;
		}
		.middle {
			stroke-dashoffset: -71px;
		}
		.bottom {
			stroke-dashoffset: -132px;
		}
	}
	&:focus-visible {
		outline: 2px solid $accentColor;
	}
	.ham {
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;
		transition: transform 400ms;
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		user-select: none;
		color: inherit;
		.line {
			fill: none;
			transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
			stroke: currentColor;
			stroke-width: 5.5;
			stroke-linecap: round;
		}
		.top {
			stroke-dasharray: 40 172;
		}
		.middle {
			stroke-dasharray: 40 111;
		}
		.bottom {
			stroke-dasharray: 40 172;
		}
	}
}

.header-menu {
	// .header-menu__popover
	&__popover {
		.MuiPaper-root {
			padding: 20px;
			max-height: 83vmin;
		}
	}
	// .header-menu__list
	&__list {
	}
	// .header-menu__item
	&__item {
		&:not(:last-child) {
			margin-bottom: 0.5em;
		}
	}
	// .header-menu__link
	&__link {
		position: relative;
		display: inline-block;
		font-size: 1.5rem;
		color: $mainDarkColor;
		text-decoration: none;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			height: 2px;
			width: 90%;
			background-color: $accentColor;
			border-radius: 10px;
			transform: translateX(-50%) scale(0);
			transition: transform 0.3s ease-in-out;
		}
		@media (any-hover: hover) {
			&:hover::after {
				transform: translateX(-50%) scale(1);
			}
		}
		&:focus-visible {
			outline: 2px solid $accentColor;
			outline-offset: 2px;
			border-radius: 6px;
		}
	}
}

.menu-accordeon {
	&.MuiPaper-root {
		padding: 0;
		box-shadow: none;
		font-size: 1.5rem;
		color: $mainDarkColor;
	}
	// .menu-accordeon__summary
	&__summary {
		&.MuiButtonBase-root {
			padding: 0;
			min-height: unset;
		}
		&.MuiButtonBase-root.Mui-focusVisible {
			background-color: #ffffff;
			outline: 2px solid $accentColor;
			outline-offset: 2px;
			border-radius: 6px;
		}
		.MuiAccordionSummary-content {
			margin: 0;
			position: relative;
			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 7%;
				height: 2px;
				width: 40%;
				background-color: $accentColor;
				border-radius: 10px;
				transform: scale(0);
				transition: transform 0.3s ease-in-out;
			}
			@media (any-hover: hover) {
				&:hover::after {
					transform: scale(1);
				}
			}
		}
		.MuiAccordionSummary-expandIconWrapper {
			.menu-accordeon__icon {
				margin-top: 4px;
				margin-bottom: 0;
			}
			&.Mui-expanded {
				.menu-accordeon__icon {
					margin-top: 0;
					margin-bottom: 4px;
				}
			}
		}
	}
	// .menu-accordeon__details
	&__details {
		&.MuiAccordionDetails-root {
			padding: 0.3em 0.65em 0;
		}
	}
	// .menu-accordeon__list
	&__list {
	}
	// .menu-accordeon__item
	&__item {
	}
	// .menu-accordeon__link
	&__link {
		position: relative;
		display: inline-block;
		font-size: 1.25rem;
		color: $mainDarkColor;
		text-decoration: none;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			height: 2px;
			width: 90%;
			background-color: $accentColor;
			border-radius: 10px;
			transform: translateX(-50%) scale(0);
			transition: transform 0.3s ease-in-out;
		}
		@media (any-hover: hover) {
			&:hover::after {
				transform: translateX(-50%) scale(1);
			}
		}
		&:focus-visible {
			outline: 2px solid $accentColor;
			outline-offset: 2px;
			border-radius: 6px;
		}
	}
}
