@import "../../assets/styles/variables.scss";
.footer {
	padding: 48px 0;
	background-color: $hoverBackgroundColor;
	@media (max-width: 48em) {
		padding: 24px 0;
	}
	// .footer__inner
	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;
		@media (max-width: 62em) {
			flex-wrap: wrap;
			justify-content: space-evenly;
		}
	}
	// .footer__copyright
	&__copyright {
		@media (max-width: 62em) {
			order: 2;
		}
		@media (max-width: 48em) {
			order: 3;
			flex: 1 0 100%;
			display: flex;
			gap: 0.5rem 1rem;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
		}
	}
	// .footer__copyright-text
	&__copyright-text {
		font-size: 1.25rem;
		line-height: 140%;
		color: #7e7e7e;
	}
	// .footer__policy
	&__policy {
		font-size: 1.25rem;
		color: $mainDarkColor;
		transition: color 0.3s ease-in-out;
		@media (any-hover: hover) {
			&:hover {
				color: $blue1Color;
			}
		}
		&:focus-visible {
			outline: 2px solid $blue1Color;
			outline-offset: 2px;
			border-radius: 6px;
		}
		&:active {
			color: $buttonActiveColor;
			transition: none;
		}
	}
	// .footer__content
	&__content {
		@media (max-width: 62em) {
			order: 1;
			flex: 1 0 100%;
		}
		@media (max-width: 48em) {
			flex: unset;
		}
	}
	// .footer__ukraine
	&__ukraine {
		position: relative;
		max-width: fit-content;
		padding-left: 32px;
		margin: 0 auto 1em;
		font-size: 1.25rem;
		line-height: 140%;
		color: $mainDarkColor;
		white-space: nowrap;
		&::before {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			content: "";
			width: 21px;
			height: 20px;
			background-image: url(../../assets/images/icons/flag-ukraine.svg);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
		}
	}
	// .footer__menu-list
	&__menu-list {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 28px;
		@media (max-width: 48em) {
			flex-direction: column;
			gap: 5px 20px;
		}
	}
	// .footer__menu-item
	&__menu-item {
	}
	// .footer__menu-link
	&__menu-link {
		position: relative;
		display: block;
		font-size: 1.25rem;
		line-height: 140%;
		color: $mainDarkColor;
		white-space: nowrap;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%) scale(0);
			width: 90%;
			height: 2px;
			background-color: $accentColor;
			transition: transform 0.3s ease-in-out;
		}
		@media (any-hover: hover) {
			&:hover::after {
				transform: translateX(-50%) scale(1);
			}
		}
		&:focus-visible {
			outline: 2px solid $blue1Color;
			outline-offset: 2px;
			border-radius: 6px;
		}
	}
	// .footer__social
	&__social {
		@media (max-width: 62em) {
			order: 3;
		}
		@media (max-width: 48em) {
			align-self: start;
			order: 2;
		}
		&-label {
			font-size: 1.25rem;
			line-height: 140%;
			color: $mainDarkColor;
			margin-bottom: 0.4em;
		}
		&-item {
		}
	}
}
