@import "../../assets/styles/variables.scss";
.social-networks {
	display: flex;
	align-items: center;
	gap: 16px;
	padding-bottom: 1em;
	@media (max-width: 62em) {
		gap: 8px;
	}
	// .social-networks__item
	&__item {
	}
	// .social-networks__link
	&__link {
		display: block;
		width: 36px;
		height: 36px;
		transition: opacity 0.3s ease-in-out;
		@media (any-hover: hover) {
			&:hover {
				opacity: 0.7;
			}
		}
		&:focus-visible {
			border-radius: 6px;
			outline: 2px solid $blue1Color;
		}
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}
}
